.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  background-color: #fff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 15px;
}

.itemD {
  background-color: #f4f6f9;
  border-top: 1px solid #eef0f4;
}

.itemD:nth-of-type(10n) {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
